.left{
background-image: url(../../images/bg2.jpg),linear-gradient(to right,rgba(0,0,0,0.4),rgba(0,0,0,0.5));
    height: 1080px;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    padding-top: 150px;
  }
  .left h1,.left h3{
    color:white;
    text-align: center;
    padding: 0;
    margin: 0;
  }
  .connect{
    text-align: center;
    margin-top: 400px;
  }
  .left button{
    background:#f29300;
    padding: 10px 60px;
    border: none;
    border-radius: 4px;
    color: white;
  }
  .scroll{
    color: white;
    text-align: center;
    display: none;
    margin-top: 300px;
  }
  .scroll p{
    padding: 0;
    margin:0;
  }
  .scroll .down{
    color: white;
    cursor: pointer;
  }
@media (max-width:1367px){
  .left{
    height: 700px;
  }
  .connect{
    text-align: center;
    margin-top: 300px;
  }
}
@media (max-width:992px){
  .connect{
    display: none;
  }
  .scroll{
    display: block;
  }
}
