.right
{
  padding: 18px 16px;
}
.recent{
  display: flex;
  flex-wrap: wrap;
}

.recent img{
  background: #f29300;
  padding: 4px;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  margin-top: 20px;
}
.recent .rctag{
  padding-left: 10px;
  font-weight: normal;
}
.visits{
  margin-top: 40px;
}
.visit{
  background: #f48400;
  padding: 20px;
  color: white;
}
.visit2{
  margin-left: 10px;
}
.visit h2,.visit p{
  margin: 0;
  padding: 0;
  font-weight: normal;
} 
.phone{
  border: 1px solid lightgrey;
  padding: 26px 16px;
  border-radius: 4px;
  margin-top: 10px;
  display: flex;
  flex-wrap:wrap;
  position: relative;
}
.phone p{
  margin: 0;
  padding: 0;
  padding-left: 8px;
  font-size: 19px;
  font-weight: normal;
  font-weight: 500;
}
.phone p:first-child{
  font-size: 14px;
  font-weight: normal;
}
.ph{
color: #f29300;
 transform: scale(1.5);
 padding-top: 7px;
}
.unlink{
  position: absolute;
  bottom: 10px;
  right: 4px;
  color: #0099ff;
  cursor: pointer;
}
.registerHeading{
  margin-top: 20px;
}
.registerHeading h2{
  margin: 0;
  padding: 0;
  text-align: center;
  color: #f29300;
}
.registerHeading h3{
  margin: 0;
  padding: 0;
  text-align: center;
  font-weight: normal;
}
.registerHeading p{
  padding-top: 16px;
}
.register select{
background: #f3f2f2;
width: 100%;
border: none;
padding: 16px;
outline: none;
border-radius: 2px;
}
.register button{
  background: #f29300;
  width: 100%;
  border: none;
  padding: 16px;
  color: white;
  margin-left: 6px;
  border-radius: 2px;
}
.footer{
  margin-top: 80px;
  text-align: center;
  font-size: 14px;
  position: relative;
}

@media (max-width:360px){
  .visit h2{
   font-size: 20px;
  } 
}
@media (max-width:1367px){
  .footer{
    margin-top: 20px
  }
  .visit{
    padding: 10px;
  }
  .right
{
  padding: 0px 16px;
}
.registerHeading p{
  padding-top: 7px;
}
}